/* Rex Core - Scaffolding */
*,
*:before,
*:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -o-box-sizing: border-box;
  -ms-box-sizing: border-box;
  box-sizing: border-box; }

html {
  font-size: 10px;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

article,
aside,
figcaption,
figure,
footer,
header,
hgroup,
main,
nav,
section {
  display: block; }

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-size: 1.6rem;
  line-height: 1.5;
  color: #4d4d4d;
  text-align: left;
  background-color: #fff; }

body:lang(ja) {
  font-size: 1.4rem; }

[tabindex="-1"]:focus {
  outline: 0 !important; }

button,
input,
select,
textarea {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  line-height: inherit; }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  margin-bottom: 0.5rem; }

p {
  margin-top: 0;
  margin-bottom: 1rem; }

abbr[title],
abbr[data-original-title] {
  text-decoration: underline;
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
  cursor: help;
  border-bottom: 0;
  text-decoration-skip-ink: none; }

address {
  margin-bottom: 1rem;
  font-style: normal;
  line-height: inherit; }

ol,
ul,
dl {
  margin-top: 0;
  margin-bottom: 1rem; }

ol ol,
ul ul,
ol ul,
ul ol {
  margin-bottom: 0; }

dt {
  font-weight: 700; }

dd {
  margin-bottom: .5rem;
  margin-left: 0; }

blockquote {
  margin: 0 0 1rem; }

b,
strong {
  font-weight: bolder; }

small {
  font-size: 80%; }

sub,
sup {
  position: relative;
  font-size: 75%;
  line-height: 0;
  vertical-align: baseline; }

sub {
  bottom: -.25em; }

sup {
  top: -.5em; }

a {
  color: #bf0000;
  text-decoration: none;
  background-color: transparent;
  cursor: pointer;
  -webkit-transition: color 0.2s cubic-bezier(0.25, 0, 0.45, 1);
  -o-transition: color 0.2s cubic-bezier(0.25, 0, 0.45, 1);
  transition: color 0.2s cubic-bezier(0.25, 0, 0.45, 1); }

a.focus,
a.hover,
a:focus,
a:hover {
  color: #f20000;
  text-decoration: underline; }

a.focus,
a:focus {
  outline: 5px auto -webkit-focus-ring-color;
  outline-offset: -2px;
  color: #f20000; }

a.active,
a:active {
  color: #8c0000; }

a.disabled-link {
  color: #d1d1d1; }

a.disabled-link:hover {
  text-decoration: none;
  cursor: default; }

figure {
  margin: 0; }

img {
  vertical-align: middle;
  border-style: none; }

svg {
  overflow: hidden;
  vertical-align: middle; }

.img-responsive {
  display: block;
  max-width: 100%;
  height: auto; }

.img-rounded {
  border-radius: 10rem; }

.img-circle {
  border-radius: 50%; }

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
  border-top: 1px solid #ebebeb; }

label {
  display: inline-block;
  margin-bottom: 0.5rem; }

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0; }

.sr-only-focusable:active,
.sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto; }

[role=button] {
  cursor: pointer; }
